.contacts {
    height: 690px;
    position: relative;
    display: flex;
    align-items: center;

    &__container{
        margin: 0;
        margin-left: auto;
        padding-right: 0;
        padding-left: 0;
    }

    // .contacts__map

    &__map {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        iframe{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    // .contacts__cart

    &__cart {
        position: relative;
        z-index: 2;
        background-color: #fff;
        margin-left: auto;
        width: 100%;
        padding: 60px 0;
        padding-left: 90px;
        padding-right: 430px;
    }

    // .contacts__title

    &__title {
        margin: 0;
        font-family: "Songer", sans-serif;
        font-weight: 900;
        font-size: 36px;
        line-height: 52px;
        letter-spacing: -0.02em;
        margin-bottom: 40px;
    }
    &__city{
        font-weight: 500;
        font-size: 24px;
        line-height: 29px;
        color: #333333;
        margin-bottom: 30px;
    }
    // .contacts__list

    &__list {
        max-width: 312px;
    }

    // .contacts__item

    &__item {
        &:not(:last-child){
            margin-bottom: 20px;
        }
        font-weight: 300;
        font-size: 18px;
        line-height: 22px;
        padding-left: 30px;
        background-repeat: no-repeat;
    }

    // .contacts__item--location

    &__item--location {
        background-image: url("../img/location.svg");
    }

    // .contacts__item--tel

    &__item--tel {
        background-image: url("../img/phone.svg");
    }

    // .contacts__item--mail

    &__item--mail {
        background-image: url("../img/email.svg");
    }

    // .contacts__email

    &__email {
    }

    // .contacts__item--adress

    &__item--adress {
        display: flex;
        flex-direction: column;
        background-image: url("../img/time.svg");
    }

    // .contacts__day

    &__day {
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: #000000;
    }

    // .contacts__time

    &__time {
        margin-bottom: 10px;
    }
}
