@media(max-width: 1700px){
    .contacts{
        &__cart{
            padding-right: 200px;
        }
    }
}
@media(max-width: 1200px){
    .header{
        &__logo{
            img{
                width: 160px;
            }
        }
        &__contact:not(:last-child){
            margin-right: 20px;
        }
    }
    .intro{
        &__pag{
            right: 170px;
        }
    }
    .info{
        &__dev{
            padding-left: 100px;
            padding-right: 50px;
        }
    }
    .contacts{
        &__cart{
            padding-right: 100px;
        }
    }
    .phone{
        .phone__list:not(:last-child){
            margin-right: 40px;
        }
    }
}
@media(max-width: 992px){
    .header{
        &__contacts{
            width: 100%;
            height: 100%;
            background-color: #fff;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 30;
            align-items: center;
            justify-content: center;
            opacity: 0;
            transition: opacity .3s ease;
            pointer-events: none;
            &.active{
                opacity: 1;
                pointer-events: all;
            }
        }
        &__burger{
            display: block;
            z-index: 31;
        }
        &__link{
            margin-left: auto;
            margin-right: 20px;
        }
    }
    .intro{
        &__box{
            min-height: 600px;
        }
    }
    .info{
        &__container{
            margin-bottom: 120px;
            flex-direction: column-reverse;
            padding: 0;
        }
        &__title{
            font-size: 28px;
            line-height: 38px;
            margin: 0 auto;
            margin-bottom: 15px;
        }
        &__news{
            padding-top: 40px;
            text-align: center;
        }
        &__dev{
            text-align: center;
            padding-left: 10px;
            padding-right: 10px;
            padding-top: 40px;
            padding-bottom: 40px;
            clip-path: none;
            &-wrapper{
                margin-top: 0;
            }
        }
        &__descr{
            font-size: 22px;
            line-height: 26px;
            margin-left: auto;
            margin-right: auto;
        }
        &__text{
            font-size: 22px;
            line-height: 22px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .contacts{
        &__cart{
            padding-right: 30px;
            padding-left: 60px;
        }
    }
    .callback{
        &__title{
            font-size: 28px;
            line-height: 40px;        
        }
        &__text{
            padding-left: 20px;
            padding-right: 10px;
        }
        &__descr{
            font-size: 18px;

        }
    }
    .phone{
        &__inner{
            flex-direction: column;
            align-items: center;
        }
        &__main{
            margin-bottom: 30px;
        }
        &__logo{
            text-align: center;
        }
        &__descr:first-child{
            padding-right: 30px;
            margin-right: 30px;
        }
    }
}
@media(max-width: 830px){
    .callback{
        &__container{
            flex-direction: column;
        }
        &__title{
            font-size: 22px;
            line-height: 34px;        
        }
        &__text{
            padding-left: 10px;
            padding-right: 0px;
            &-wrapper{
                margin-bottom: 30px;
            }
        }
        &__descr{
            font-size: 17px;
        }
        &__input{
            width: 260px;
        }
        &__area{
            width: 280px;
        }
        &__form{
            align-items: center;
        }
        &__next{
            align-self: center;
        }
    }
}
@media(max-width: 768px){
    .intro{
        &__pag{
            right: 100px;
        }
        &__title{
            font-size: 52px;
            line-height: 65px;
        }
    }
    .info{
        &__container{
            margin-bottom: 60px;
        }
    }
    .contacts{
        flex-direction: column-reverse;
        height: auto;
        &__map{
            position: static;
            height: 600px;
        }
        &__container{
            margin: 0 auto;
        }
        &__cart{
            padding-left: 15px;
            padding-right: 15px;
        }
        &__city{
            text-align: center;
        }
        &__title{
            text-align: center;
        }
    }
    .phone{
        &__descrs{
            flex-direction: column;
        }
        &__descr:first-child{
            &::after{
                display: none;
            }
            margin: 0;
            padding: 0;
            margin-bottom: 15px;
        }
    }
}
@media(max-width: 700px){
    .phone{
        &__list{
            flex: 0 1 50%;
            margin: 0;
            margin-bottom: 30px;
        }
        .phone__list:not(:last-child){
            margin: 0;
        }
    }
}
@media(max-width: 576px){
    .header{
        &__contacts{
            flex-direction: column;
        }
        &__link{
            display: none;
            margin: 0;
            font-size: 20px;
            &--mobile{
                display: block;
            }
        }
        &__contact{
            text-align: center;
            margin-bottom: 25px;
        }
        &__location{
            font-size: 24px;
            margin-bottom: 10px;
        }
        &__tel{
            font-size: 26px;
        }
    }
    .intro{
        &__pag{
            bottom: 100px;
        }
        &__box{
            min-height: 440px;
        }
    }
    .contacts{
        &__item{
            font-size: 16px;
        }
    }
}
@media(max-width: 500px){
    .intro{
        &__title{
            font-size: 42px;
        }
        &__fract{
            display: none;
        }
        &__pag{
            bottom: 25px;
            right: 25px;
        }
    }
    .phone{
        &__list{
            flex: 0 1 100%;
            margin-bottom: 30px;
        }
        &__lists{
            text-align: center;
        }
        &__tel-main{
            text-align: center;
        }
        &__item:not(:last-child){
            margin-bottom: 20px;
        }
        .phone__list:not(:last-child){
            margin-bottom: 20px;
        }
    }
}
@media(max-width: 420px){
    .header{
        &__link{
            padding-left: 10px;
            padding-right: 10px;
            &::after{
                display: none;
            }
        }
    }
    .intro{
        &__title{
            font-size: 32px;
            line-height: 40px;
        }
        &__descr{
            font-size: 20px;
        }
    }
    .info{
        &__container{
            margin-bottom: 70px;
        }
        &__title{
            font-size: 20px;
            line-height: 28px;
        }
        &__link{
            &::after{
                display: none;
            }
            padding: 14px 20px;
            font-size: 14px;
        }
    }
    .callback{
        padding-top: 0;
        &__text{
            clip-path: none
        }
        &__container{
            margin-right: 0;
            padding-right: 0;
        }
    }
}