.info {

    // .info__container

    &__container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 180px;
    }

    // .info__news

    &__news {
        padding-top: 70px;
    }

    // .info__descr

    &__descr {
        max-width: 440px;
        font-weight: normal;
        font-size: 24px;
        line-height: 29px;
        margin: 0;
        margin-bottom: 33px;
    }

    // .info__link

    &__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        background-color: $BurningOrange;
        padding: 14px 30px;
        padding-right: 50px;
        position: relative;
        display: inline-block;
        transition: all .3s ease;
        &::after{
            width: 12px;
            height: 12px;
            border-radius: 50%;
            content: "";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 28px;
            background-color: #fff;
            transition: background-color .3s ease;
        }
        &:hover{
            background-color: #fc4a0f;
        }
        &--white{
            color: $BurningOrange;
            background-color: #FFFFFF;
            transition: all .3s ease;
            &:hover{
                color: #fff;
            }
        }
    }

    // .info__dev

    &__dev {
        background: $BurningOrange;
        padding: 50px 95px;
        clip-path: polygon(4% 0, 100% 0%, 100% 100%, 0% 100%);
        padding-left: 143px;
        position: relative;
        z-index: 3;
        &-wrapper{
            position: relative;
            margin-top: -60px;
            &::after{
                height: 100%;
                background-color: $BurningOrange;
                width: 100vh;
                left: 99%;
                top: 0;
                content: "";
                position: absolute;
                z-index: 3;
            }
        }
    }

    // .info__title

    &__title {
        font-family: "Songer", sans-serif;
        font-weight: 900;
        font-size: 36px;
        line-height: 52px;
        letter-spacing: -0.02em;
        margin: 0;
        max-width: 390px;
        color: #fff;
        margin-bottom: 13px;
    }

    // .info__text

    &__text {
        margin: 0;
        font-size: 24px;
        line-height: 29px;
        letter-spacing: -0.02em;
        color: #fff;
        max-width: 385px;
        margin-bottom: 26px;
    }
}
