.phone {
    background: #6B3D2E;
    padding-top: 50px;
    // .phone__container

    &__container {
    }

    // .phone__inner

    &__inner {
        display: flex;
        justify-content: space-between;
        max-width: 1100px;
        padding-bottom: 40px;
    }

    // .phone__main

    &__main {
        display: flex;
        flex-direction: column;
    }

    // .phone__logo

    &__logo {
        margin-bottom: 50px;
    }

    // .phone__tel-main

    &__tel-main {
        display: flex;
        flex-direction: column;
        padding-left: 44px;
        background-image: url("../img/white-phone.svg");
        background-repeat: no-repeat;
        &:not(:last-child){
            margin-bottom: 40px;
        }
        span{
            transition: background-color .3s ease;
        }
        &:hover{
            span{
                color: #cacaca;
            }
        }
    }

    // .phone__city

    &__city {
        font-weight: 500;
        font-size: 24px;
        line-height: 29px;
        color: #FFFFFF;
    }

    // .phone__number

    &__number {
        font-weight: 300;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
    }

    // .phone__lists

    &__lists {
        display: flex;
        flex-wrap: wrap;
    }

    // .phone__list

    &__list {
        &:not(:last-child){
            margin-right: 100px;
        }
    }

    // .phone__item

    &__item {
        &:not(:last-child){
            margin-bottom: 10px;
        }
        span{
            transition: background-color .3s ease;
        }
        &:hover{
            span{
                color: #cacaca;
            }
        }
    }

    // .phone__wrapper

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    // .phone__name

    &__name {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;

    }

    // .phone__num

    &__num {
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
    }

    // .phone__descrs

    &__descrs {
        display: flex;
        padding: 46px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
    }

    // .phone__descr

    &__descr {
        font-size: 15px;
        line-height: 18px;
        color: #FFFFFF;
        margin: 0;
        &:first-child{
            padding-right: 85px;
            margin-right: 85px;
            position: relative;
            &::after{
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 108px;
                width: 1px;
                background: rgba(255, 255, 255, 0.1);;
                content: "";
            }
        }
    }
}

