.intro {
    // .intro__slider

    &__slider {
        .swiper-slide{
            position: relative;
        }
    }

    // .intro__box

    &__box {
        min-height: 708px;
        max-width: 1180px;
        padding: 0 15px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .intro__text

    &__text {
        position: relative;
        z-index: 2;
        max-width: 465px;
    }

    // .intro__title

    &__title {
        font-weight: 900;
        font-family: "Songer", sans-serif;
        font-size: 64px;
        line-height: 92px;
        letter-spacing: -0.02em;
        color: #FFFFFF;
        margin: 0;
        margin-bottom: 12px;
    }

    // .intro__descr

    &__descr {
        font-weight: normal;
        font-size: 24px;
        line-height: 29px;
        color: #FFFFFF;
        max-width: 350px;
        margin: 0;
        margin-bottom: 35px;
    }

    // .intro__link

    &__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        padding: 14px 28px;
        padding-right: 50px;
        position: relative;
        background: #FF6A39;
        border-radius: 1px;
        transition: all .3s ease;
        &::after{
            content: "";
            position: relative;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 30px;
            background-color: #fff;
            transition: background-color .3s ease;
        }
        &:hover{
            background-color: #fff;
            color: $BurningOrange;
            &::after{
                background-color: $BurningOrange;
            }
        }
    }
    &__fract{
        text-align: right;
    }
    // .intro__pag

    &__pag {
        position: absolute;
        z-index: 2;
        right: 345px;
        bottom: 220px;
        font-style: normal;
        font-weight: 900;
        font-family: Songer, sans-serif;
        color: #FFFFFF;
        font-size: 0;
        .swiper-pagination-current{
            font-family: Songer, sans-serif;
            font-weight: 900;
            font-size: 48px;
            line-height: 69px;
            letter-spacing: 0.125em;
            color: #FFFFFF;
        }
        .swiper-pagination-total{
            font-weight: 400;
            font-size: 36px;
            line-height: 44px;
            letter-spacing: 0.125em;
            color: #FFFFFF;
            &::before{
                content: "/";
            }
        }
    }

    // .intro__img

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    
    &__bullet{
        margin-top: 11px;
        .swiper-pagination-bullet{
            width: 12px;
            height: 12px;
            background: #FFFFFF;
            opacity: 1;
            position: relative;
            &::before{
                background-color: $BurningOrange;
                position: absolute;
                content: "";
                width: 4px;
                height: 4px;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                opacity: 0;
                transition: opacity .3s ease;
            }
            margin: 0;
            &:not(:last-child){
                margin-right: 20px;
                &::after{
                    opacity: 1;
                    width: 24px;
                    height: 1px;
                    background-color: #fff;
                    content: "";
                    position: absolute;
                    left: 100%;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
        .swiper-pagination-bullet-active::before{
            opacity: 1;
        }
    }
}

