.callback {
    background-color: $BurningOrange;
    padding: 50px 0;
    // .callback__container

    &__container {
        display: flex;
        max-width: 1110px;
        margin: 0 auto;
        padding: 0 15px;
        justify-content: space-between;
        align-items: center;
    }

    // .callback__text

    &__text {
        background-color: #FFFFFF;
        padding: 50px;
        clip-path: polygon(0 0, 100% 0, 95% 100%, 0% 100%);
        &-wrapper{
            position: relative;
            &::before{
                width: 100vh;
                height: 100%;
                position: absolute;
                right: 99%;
                content: "";
                top: 0;
                background-color: #FFFFFF;
            }
        }
    }

    // .callback__title

    &__title {
        font-family: "Songer", sans-serif;
        font-weight: 900;
        font-size: 36px;
        line-height: 52px;
        letter-spacing: -0.02em;
        color: #333333;
        margin: 0;
        margin-bottom: 10px;
        max-width: 470px;
    }

    // .callback__descr

    &__descr {
        margin: 0;
        font-weight: 300;
        font-size: 20px;
        line-height: 24px;
        color: #333333;
        max-width: 410px;
    }

    // .callback__form

    &__form {
        display: flex;
        flex-direction: column;
    }

    // .callback__label

    &__label {
        font-family: "Roboto", sans-serif;
        font-weight: normal;
        font-size: 17px;
        line-height: 20px;
        color: #FFFFFF;
        margin-bottom: 2px;
    }

    // .callback__input

    &__input {
        border: none;
        width: 300px;
        height: 47px;
        padding-left: 22px;
        font-family: "Roboto", sans-serif;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        &:placeholder{
            color: #B0B0B0;
        }
        border: 1px solid #FF6A39;
        border-radius: 4px;
        margin-bottom: 10px;
    }

    // .callback__area

    &__area {
        padding-top: 12px;
        resize: none;
        width: 328px;
        height: 99px;
        border: 1px solid #FF6A39;
        border-radius: 4px;
        padding-left: 22px;
        font-family: "Roboto", sans-serif;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        &:placeholder{
            color: #B0B0B0;
        }
        margin-bottom: 20px;
    }

    // .callback__check

    &__check {
        margin-bottom: 20px;
        font-family: Roboto, sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        position: relative;
        padding-left: 35px;
        input{
            appearance: none;
            position: absolute;
            top: 0;
            left: 0;
            &:checked + .callback__checkbox::after{
                opacity: 1;
            }
        }
    }

    // .callback__checkbox

    &__checkbox {
        position: absolute;
        top: 0;
        left: 0;
        width: 23px;
        height: 23px;
        border: 1px solid #FF6A39;
        border-radius: 4px;
        background-color: #FFFFFF;
        &::after{
            transition: opacity .3s ease;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: url("../img/check.svg");
            width: 12px;
            height: 10px;
            content: "";
            opacity: 0;
        }
    }

    &__next{
        background: #FFFFFF;
        color: $BurningOrange;
        padding: 14px 25px;
        padding-right: 50px;
        position: relative;
        align-self: flex-start;
        &::after{
            position: absolute;
            right: 25px;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            top: 50%;
            background-color: $BurningOrange;
            transform: translateY(-50%);
            content: "";
        }
        transition: background-color .3s ease;
        &:hover{
            background-color: #dddddd;
        }
    }
    &__warn{
        max-width: 290px;
        display: inline-block;
    }
}
