@import "../mixins/burger";

.header {
    padding-top: 15px;
    padding-bottom: 15px;
    // .header__container

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__contacts

    &__contacts {
        display: flex;
        overflow: auto;
    }

    // .header__contact

    &__contact {
        display: flex;
        flex-direction: column;
        &:not(:last-child){
            margin-right: 50px;
        }
        position: relative;
        padding-left: 44px;
        &::before{
            background: url("../img/tel.svg");
            position: absolute;
            left: 0;
            top: 0;
            content: "";
            width: 24px;
            height: 24px;
        }
        span{
            transition: color .3s ease;
        }
        &:hover{
            span{
                color: #8a8888;
            }
        }
    }

    // .header__location

    &__location {
        font-weight: 500;
        line-height: 22px;
    }

    // .header__tel

    &__tel {
        font-size: 20px;
        line-height: 24px;
    }

    // .header__link

    &__link {
        background: #FFFFFF;
        border: 2px solid $BurningOrange;
        border-radius: 1px;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $BurningOrange;
        padding: 14px 25px;
        padding-right: 48px;
        position: relative;
        transition: all .3s ease;
        &:hover{
            background-color: $BurningOrange;
            color: #FFFFFF;
            &::after{
                background-color: #FFFFFF;
            }
        }
        &--mobile{
            display: none;
        }
        &::after{
            transition: background-color .3s ease;
            content: "";
            width: 12px;
            height: 12px;
            border-radius: 50%;
            right: 25px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            background-color: $BurningOrange;
    }   }
    

    &__burger{
        @include burger;
        --burgerWidth: 30px;
        --burgerHeight: 20px;
        --burgerColor: black;
        --burgerLine: 2px;
        display: none;
    }
}
